:root {
    --grey: #808080;
    --dark-grey: #707070;
    --light-purple: #3b0062;
    --highlight-yellow: #fab83f;
    --blue: #2c1587;
    --light-blue: #0d6efd;
    --danger-red: #a30000;
    --highlight-white: #ffffff;
    --dark-white: #f5f5f5;
}


* {
    box-sizing: border-box;
    margin: 0;
}

body {
    font-family: sans-serif;
}
.main-header {

    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
}

.content {
    margin-top: 3.5rem;
    padding: 1rem;
}

@media (min-width: 768px) {
    .content {
        margin-top: 3.5rem;
        padding: 1rem 2rem;
    }
}
.backdrop {
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 100;
  position: fixed;
  left: 0;
  top: 0;
  transition: opacity 0.3s ease-out;
  opacity: 1;
}

.toolbar {
    width: 100%;
    height: 4rem;
    background: #2c1587;
}
.mobile-toggle {
  background: transparent;
  border: none;
  display: flex;
  width: 2.5rem;
  height: 80%;
  flex-direction: column;
  justify-content: space-evenly;
  cursor: pointer;
  padding: 0;
  margin-right: 1rem;
}

.mobile-toggle__bar {
  width: 2.5rem;
  height: 4px;
  background: var(--highlight-white);
}

@media (min-width: 768px) {
  .mobile-toggle {
    display: none;
  }
}

.logo {
  font-size: 1rem;
  font-weight: bold;
  border: 1px solid var(--highlight-white);
  margin-top: 0.4rem;
  padding: 0.5rem;
  color: var(--highlight-white);
}

.navigation-item {
  padding: 0 1.5rem;
}

.navigation-item a {
  text-decoration: none;
  color: var(--highlight-white);
}

.navigation-item.mobile {
  font-size: 1.75rem;
  margin: 0.5rem 0;
}

.navigation-item.mobile a {
  color: var(--light-purple);
}

.navigation-item a:hover,
.navigation-item a:active,
.navigation-item a.active {
  color: var(--highlight-yellow);
  pointer-events: none;
}

.navigation-item:last-of-type {
  padding-right: 0;
}

.navigation-item button {
  font: inherit;
  background: transparent;
  border: none;
  color: var(--highlight-white);
  cursor: pointer;
  padding: 0;
}

.navigation-item button:hover,
.navigation-item button:active {
  color: var(--highlight-yellow);
}

.main-nav {
  height: 100%;
  padding: 0 1rem;
  display: flex;
  align-items: center;
}

.spacer {
  flex: 1 1;
}

.main-nav__items {
  list-style: none;
  padding: 0;
  margin: 0 1.5rem;
  display: none;
}

.main-nav__logo a {
  text-decoration: none;
}

@media (min-width: 768px) {
  .main-nav__items {
    display: flex;
  }
}

.mobile-nav {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 15rem;
  max-width: 70%;
  background: var(--dark-white);
  box-shadow: 1px 0 8px rgba(0, 0, 0, 0.26);
  transition: transform 0.3s ease-out;
  transform: translateX(-100%);
  z-index: 200;
  padding: 3rem 2rem;
}

.mobile-nav.open {
  transform: translateX(0);
}

.mobile-nav__items {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.mobile-nav__items.mobile {
  flex-direction: column;
}

.button {
  font: inherit;
  border: 1px solid var(--light-purple);
  color: var(--light-purple);
  background: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
}

.button:focus {
  outline: none;
}

.button:disabled {
  background: #ccc;
  color: #888888;
  cursor: not-allowed;
  border: #ccc;
  box-shadow: none;
}

.button:disabled:hover,
.button:disabled:active {
  background: #ccc;
  color: #888888;
  border: #ccc;
}

.button:hover,
.button:active {
  background: var(--light-purple);
  color: var(--highlight-white);
}

.button--flat {
  border: none;
}

.button--flat:hover,
.button--flat:active {
  background: rgba(59, 0, 98, 0.3);
  color: var(--light-purple);
}

.button--raised {
  background: var(--light-blue);
  color: var(--highlight-white);
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.26);
}

.button--raised:hover,
.button--raised:active {
  background: #520288;
}

.button.button--accent {
  border-color: var(--highlight-yellow);
  color: var(--highlight-yellow);
}

.button--accent:hover,
.button--accent:active {
  border-color: var(--highlight-yellow);
  color: var(--light-purple);
}

.button--flat.button--accent:hover,
.button--flat.button--accent:active {
  background: rgba(250, 184, 63, 0.3);
  color: var(--highlight-yellow);
}

.button.button--accent.button--raised {
  background: var(--highlight-yellow);
  color: var(--light-purple);
}

.button--raised.button--accent:hover,
.button--raised.button--accent:active {
  background: #fbc766;
}

.button.button--danger {
  border-color: var(--danger-red);
  color: var(--danger-red);
}

.button--danger:hover,
.button--danger:active {
  background: var(--danger-red);
  color: var(--highlight-white);
}

.button--flat.button--danger:hover,
.button--flat.button--danger:active {
  background: rgba(163, 0, 0, 0.3);
  color: var(--danger-red);
}

.button.button--danger.button--raised {
  background: var(--danger-red);
  color: var(--highlight-white);
}

.button--raised.button--danger:hover,
.button--raised.button--danger:active {
  background: #c00000;
}

.button.button--success {
  border-color: #00b359;
  color: #00b359;
}

.button--success:hover,
.button--success:active {
  background: #00b359;
  color: var(--highlight-white);
}

.button--flat.button--success:hover,
.button--flat.button--success:active {
  background: rgba(0, 179, 90, 0.3);
  color: #00b359;
}

.button.button--success.button--raised {
  background: #00b359;
  color: var(--highlight-white);
}

.button--raised.button--success:hover,
.button--raised.button--success:active {
  background: #00a151;
}

.modal2 {
  position: fixed;
  width: 90%;
  left: 5%;
  top: 20vh;
  display: block;
  background: var(--highlight-white);
  border-radius: 5px;
  z-index: 500;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.modal2__header {
  border-bottom: 2px solid var(--light-purple);
}

.modal2__header h1 {
    font-size: 1.5rem;
    color: var(--light-purple);
    margin: 1rem;
}

.modal2__content {
    padding: 1rem;
}

.modal2__actions {
    padding: 1rem;
    text-align: right;
}

.modal2__actions button {
    margin: 0 0.5rem;
}

@media (min-width: 768px) {
    .modal2 {
        width: 40rem;
        left: calc((100% - 40rem) / 2);
    }
}
.post {
  margin: 1rem 0;
  border: 1px solid var(--light-purple);
  border-radius: 5px;
  padding: 0.5rem;
}

.post__meta {
  font-size: 1rem;
  color: var(--dark-grey);
  margin: 0;
}

.post__title {
  font-size: 1.5rem;
  margin: 1rem 0;
  color: var(--light-purple);
}

.post__image {
  height: 15rem;
  width: 100%;
}

.post__actions {
  text-align: right;
}

@media (min-width: 768px) {
  .post {
    padding: 1rem;
    width: 40rem;
    margin-left: auto;
    margin-right: auto;
  }
}

.input {
  margin: 1rem 0;
  width: 100%;
}

.input label {
  display: block;
  text-transform: uppercase;
  margin-bottom: 0.25rem;
}

.input input,
.input textarea {
  display: block;
  font: inherit;
  padding: 0.25rem 0.5rem;
  width: 100%;
  border-radius: 3px;
  border: 1px solid #ccc;
}

.input .touched.invalid {
  border-color: red;
  background: #ffc2c2;
}

.input input:focus,
.input textarea:focus {
  outline: none;
  border-color: var(--light-purple);
  color: var(--light-purple);
}

.image {
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
}

.paginator__controls {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
}

.paginator__control {
  width: 5rem;
  padding: 0.25rem 0;
  margin: 0 1rem;
  border: 1px solid var(--light-purple);
  background: transparent;
  font: inherit;
  cursor: pointer;
  font-size: 1rem;
  color: var(--light-purple);
}

.paginator__control:hover,
.paginator__control:active {
  color: var(--highlight-yellow);
  border-color: var(--highlight-yellow);
}

.paginator__control:focus {
  outline: none;
}

.loader {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}

.loader div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid var(--light-purple);
  border-radius: 50%;
  animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #3b0062 transparent transparent transparent;
}

.loader div:nth-child(1) {
  animation-delay: -0.45s;
}

.loader div:nth-child(2) {
  animation-delay: -0.3s;
}

.loader div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.feed__status {
  width: 70%;
  display: flex;
  flex-direction: column;
  margin: 1rem auto;
}

.feed__status form {
  display: flex;
  align-items: center;
}

.feed__status p {
  font-size: 0.8rem;
}

.feed__status form * {
  margin: 0 0.05rem;
}

.feed__control {
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.new-post__preview-image {
  width: 15rem;
  height: 7rem;
}

.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  /* opacity: 0.5;
  transition: opacity 300ms; */

  animation: fadeEnter 0.8s ease-out;
}

.fade-enter-done {
  opacity: 1;
}

.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  animation: fadeExit 1s ease-out;
}

.fade-exit-done {
  opacity: 0;
}

@keyframes fadeEnter {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 0.5;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeExit {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }

  100% {
    opacity: 0;
  }
}


@media (min-width: 768px) {
  .feed__status {
    width: 40rem;
  }
}

.single-post {
